}
.jj {
  height: 100%;
  text-align: left;
  font-size: 2vw;
  padding-top: 6%;
  padding-left: 3%;
  margin: 0;
}
.mainpor {
  width: 100%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main {
  width: 80%;
  height: 80%;
  background-color: white;
  overflow-y: scroll;
  display: flex;
  flex-wrap: wrap;
}

.main::-webkit-scrollbar {
  width: 8px;
}

.main::-webkit-scrollbar-track {
  /* -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); */
  background: linear-gradient(180deg, #f7f0f7 20.71%, #bbd9e9 100%);

  border-radius: 6px;
}

.main::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 255, 255, 0.692);
}
